module.exports = "import {\n  Component,\n  ChangeDetectionStrategy,\n  ViewChild,\n  TemplateRef,\n  OnInit\n} from '@angular/core';\nimport {\n  startOfDay,\n  endOfDay,\n  subDays,\n  addDays,\n  endOfMonth,\n  isSameDay,\n  isSameMonth,\n  addHours\n} from 'date-fns';\nimport { Subject } from 'rxjs';\nimport { NgbModal } from '@ng-bootstrap/ng-bootstrap';\nimport {\n  CalendarEvent,\n  CalendarEventAction,\n  CalendarEventTimesChangedEvent,\n  CalendarView\n} from 'angular-calendar';\n\nimport {\n  Http,\n  Headers,\n  RequestOptionsArgs,\n  RequestOptions\n} from '@angular/http';\n\nconst colors: any = {\n  red: {\n    primary: '#ad2121',\n    secondary: '#FAE3E3'\n  },\n  blue: {\n    primary: '#1e90ff',\n    secondary: '#D1E8FF'\n  },\n  yellow: {\n    primary: '#e3bc08',\n    secondary: '#FDF1BA'\n  }\n};\n\n@Component({\n  selector: 'mwl-demo-component',\n  changeDetection: ChangeDetectionStrategy.OnPush,\n  styleUrls: ['styles.css'],\n  templateUrl: 'template.html'\n})\nexport class DemoComponent implements OnInit {\n  @ViewChild('modalContent')\n  modalContent: TemplateRef<any>;\n\n  view: CalendarView = CalendarView.Month;\n\n  CalendarView = CalendarView;\n\n  viewDate: Date = new Date();\n\n  modalData: {\n    action: string;\n    event: CalendarEvent;\n  };\n\n  actions: CalendarEventAction[] = [\n    {\n      label: '<i class=\"fa fa-fw fa-pencil\"></i>',\n      onClick: ({ event }: { event: CalendarEvent }): void => {\n        this.handleEvent('Edited', event);\n      }\n    },\n    {\n      label: '<i class=\"fa fa-fw fa-times\"></i>',\n      onClick: ({ event }: { event: CalendarEvent }): void => {\n        this.events = this.events.filter(iEvent => iEvent !== event);\n        this.handleEvent('Deleted', event);\n      }\n    }\n  ];\n\n  refresh: Subject<any> = new Subject();\n\n  doctorss: any[];\n  doctor1: number;\n\n  patientss: any[];\n  patient1: number;\n\n  dateEvent: Date;\n\n  alertNew: boolean = false;\n\n  events: CalendarEvent[] = [\n    // {\n    //   start: subDays(startOfDay(new Date()), 1),\n    //   end: addDays(new Date(), 1),\n    //   title: 'A 3 day event',\n    //   color: colors.red,\n    //   actions: this.actions,\n    //   allDay: true,\n    //   resizable: {\n    //     beforeStart: true,\n    //     afterEnd: true\n    //   },\n    //   draggable: true\n    // },\n    // {\n    //   start: startOfDay(new Date()),\n    //   title: 'An event with no end date',\n    //   color: colors.yellow,\n    //   actions: this.actions\n    // },\n    // {\n    //   start: subDays(endOfMonth(new Date()), 3),\n    //   end: addDays(endOfMonth(new Date()), 3),\n    //   title: 'A long event that spans 2 months',\n    //   color: colors.blue,\n    //   allDay: true\n    // },\n    // {\n    //   start: addHours(startOfDay(new Date()), 2),\n    //   end: new Date(),\n    //   title: 'A draggable and resizable event',\n    //   color: colors.yellow,\n    //   actions: this.actions,\n    //   resizable: {\n    //     beforeStart: true,\n    //     afterEnd: true\n    //   },\n    //   draggable: true\n    // }\n  ];\n\n  activeDayIsOpen: boolean = true;\n\n  constructor(private modal: NgbModal, private http: Http) {\n\n  }\n\n  ngOnInit() {\n    this.doctors();\n    this.commitments();\n    this.patients();\n    document.getElementById('today').click();\n  }\n\n  doctors() {\n    this.http\n      .get('http://ec2-54-145-167-89.compute-1.amazonaws.com/api/medicos')\n      .toPromise()\n      .then(resp => {\n        this.doctorss = resp.json().data.map(element => {\n          return { id: element.id, name: element.nome_medico };\n        });\n      })\n      .catch(error => {\n        console.error(error.status);\n      });\n  }\n\n  patients() {\n    this.http\n      .get(`http://ec2-54-145-167-89.compute-1.amazonaws.com/api/pacientes`)\n      .toPromise()\n      .then(resp => {\n        this.patientss = resp.json().data.map(element => {\n          return { id: element.id, name: element.nome };\n        });\n        console.log(this.patientss);\n      })\n      .catch(error => {\n        console.error(error.status);\n      });\n  }\n\n  commitments() {\n    this.events = [];\n    this.http\n      .get(`http://ec2-54-145-167-89.compute-1.amazonaws.com/api/agendas?medico_id=${this.doctor1}`)\n      .toPromise()\n      .then(resp => {\n        resp.json().data.forEach(element => {\n          this.events.push({\n            id: element.id,\n            title: element.paciente.nome,\n            start: startOfDay(element.datahora),\n            end: endOfDay(element.datahora),\n            color: colors.red,\n            draggable: true,\n            resizable: {\n              beforeStart: true,\n              afterEnd: true\n            }\n          });\n        });\n        this.refresh.next();\n      })\n      .catch(error => {\n\n      });\n  }\n\n  delete(id: number) {\n    this.http\n      .delete(`http://ec2-54-145-167-89.compute-1.amazonaws.com/api/agendas/${id}`)\n      .toPromise()\n      .then(resp => {\n        this.commitments();\n      })\n      .catch(error => {\n        console.error(error.status);\n      });\n  }\n\n  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {\n    if (isSameMonth(date, this.viewDate)) {\n      this.viewDate = date;\n      if (\n        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||\n        events.length === 0\n      ) {\n        this.activeDayIsOpen = false;\n      } else {\n        this.activeDayIsOpen = true;\n      }\n    }\n  }\n\n  eventTimesChanged({\n    event,\n    newStart,\n    newEnd\n  }: CalendarEventTimesChangedEvent): void {\n    event.start = newStart;\n    event.end = newEnd;\n    this.handleEvent('Dropped or resized', event);\n    this.refresh.next();\n  }\n\n  handleEvent(action: string, event: CalendarEvent): void {\n    this.modalData = { event, action };\n    this.modal.open(this.modalContent, { size: 'lg' });\n  }\n\n  addEvent(): void {\n\n    if(!this.dateEvent || !this.doctor1 || !this.patient1) {\n      this.alertNew = true;\n    } else {\n\n      this.http\n      .post(`http://ec2-54-145-167-89.compute-1.amazonaws.com/api/agendas`, {\n        datahora: this.dateEvent,\n        medico_id: this.doctor1,\n        paciente_id: this.patient1\n      })\n      .toPromise()\n      .then(resp => {\n        this.commitments();\n      })\n      .catch(error => {\n        console.error(error.status);\n      });\n\n    }\n\n    /*this.events.push({\n      title: 'New event',\n      start: startOfDay(new Date()),\n      end: endOfDay(new Date()),\n      color: colors.red,\n      draggable: true,\n      resizable: {\n        beforeStart: true,\n        afterEnd: true\n      }\n    });\n    this.refresh.next();*/\n  }\n}\n"